#scrollBtn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 60px;
  height: 60px;
  border: none;
  outline: none;
  border-radius: 50%;
  background-color: rgba(153, 152, 152, 0.733);
  font-size: 30px;
  color: rgb(255, 255, 255);
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: $transitionTimingFunction $transitionDuration;
  &:hover {
    background-color: rgba(124, 124, 124, 0.733);
  }
}
