*,
::after,
::before {
  padding: 0;
  margin: 0;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
body {
  font-family: $fonfFamily;
  color: $fontColorPrimary;
  font-style: normal;
}
.container {
  width: 100%;
  max-width: 1280px;
  padding: 0 20px;
  margin: 0 auto;

  @include tablet {
    padding: 0 32px;
  }
}
svg {
  vertical-align: middle;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
