@keyframes loaderspin {
  0% {
    transform: translate(-50%, -50%)rotate(0);
  }

  100% {
    transform: translate(-50%, -50%)rotate(360deg);
  }
}

@keyframes loaderpulse {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.2);
  }
}

.hidden {
  display: none;
}

#pre-load {
  height: 100%;
  width: 100%;
  z-index: 9999;
  background-color: #000000b4;
  margin-top: 0;
  position: fixed;
  top: 0;
}

.loader__container {
  width: 150px;
  height: 150px;
  border: 5px solid #ebebec;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader__container:before {
  content: "";
  width: 150px;
  height: 150px;
  border-top: 4px solid #ff6b02;
  border-radius: 50%;
  animation: 1.8s ease-in-out infinite loaderspin;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader__icon {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader__img {
  width: 50px;
  height: auto;
  animation: .9s infinite alternate loaderpulse;
  display: flex;
}

@keyframes animate360 {
  0% {
    transform: rotateY(0);
  }

  50% {
    transform: rotateY(180deg);
  }

  100% {
    transform: rotateY(360deg);
  }
}

